<template>
  <div class="main">

    <h1 class="logo">Camera monitoring</h1>
    <p>Please enter the URL of the youtube video</p>

    <div class="InputAddOn">
      <input class="input_source InputAddOn-field" v-model="video" type="text"></input>
      <button class="InputAddOn-item cyan" v-on:click="add">Add +</button>

    </div>

    <p v-if="empty" style="color: red;">You have not entered a url</p>

    <div class="videos">
      <iframe v-for="n in videos" :width="width" :height="height" :src="n" frameborder="0" allowfullscreen></iframe>
    </div>

  </div>
</template>

<script>
export default {
  name: 'MainPage',
  data() {
    return {
      videos: [],
      video: '',
      n_videos: 0,
      width: 0,
      height: 0,
      empty: false,
    }
  },

  methods: {



    add: function(event) {
      if (this.video !== '') {
        this.videos.push('https://www.youtube.com/embed/' + youtube_parser(this.video) + '?autoplay=1&mute=1');
        this.video = '';
        this.n_videos = Math.trunc(Math.sqrt(this.videos.length + 3));
        this.width = 1280 / this.n_videos;
        this.height = 720 / this.n_videos;
        this.empty = false;

      } else {
        this.empty = true;
      }

    }
  }
}

function youtube_parser(url) {
  var regExp = /^.*((youtu.be\/)|(v\/)|(\/u\/\w\/)|(embed\/)|(watch\?))\??v?=?([^#\&\?]*).*/;
  var match = url.match(regExp);
  return (match && match[7].length == 11) ? match[7] : false;
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.InputAddOn {
  margin: 0pc auto;
  display: flex;
  width: 50%;
}

.InputAddOn-field {
  flex: 1;
  width: 50%;
}

.InputAddOn-item {
  border: none;
  color: white;
  font-weight: bold;
  padding-left: 30px;
  padding-right: 30px;
  cursor: pointer;
}

.main {
  min-height: 100%;
  text-align: center;
  color: white;
  margin-top: 15%;
}

.input_source {
  padding: 10px;
  padding-left: 20px;
  padding-right: 20px;
  border: none;
}

.videos {
  margin-top: 50px;
  margin-top: 50px;
}

.logo {
  position: relative;
  -webkit-animation: mymove 5s infinite;
  /* Safari 4.0 - 8.0 */
  animation: mymove 4s infinite;
}


/* Safari 4.0 - 8.0 */

@-webkit-keyframes mymove {
  0% {
    top: 10px;
  }
  50% {
    top: -10px;
  }

  100% {
    top: 10px;
  }
}


/* Standard syntax */

@keyframes mymove {
 0% {
    top: 10px;
  }
  50% {
    top: -10px;
  }

  100% {
    top: 10px;
  }
}
</style>



